.multiModelVeiwer {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	width: 100%;
	height: 100%;
	position: relative;
}
.multiModelVeiwer__scene {
	z-index: 2;
	display: flex;
	width: 100%;
	height: 100%;
}
.multiModelVeiwer__ctrlBar {
	position: absolute;
	bottom: 0;
	display: flex;
	flex-direction: row;
	justify-content: space-around;
	width: 95%;
	height: max-content;
	align-items: center;
	z-index: 5;
}
.multiModelVeiwer__btns-title {
	display: flex;
	flex-direction: row;
	width: max-content;
	align-items: center;
}
#multiModelViewer__interact {
	display: flex;
	position: relative;
	font-size: 0.5;
	justify-content: center;
	align-items: center;
}
#interact-icon {
	/* font-size: calc((2vh + 2vw) / 0.6); */
}
#no-interact-icon {
	position: absolute;
	/* font-size: calc((2vh + 2vw) / 0.5); */
}
