} */
.scene__no-interact-cover {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	bottom: 0;
	background: transparent;
	color: white;
}
.multiModelVeiwer__loading {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: 100%;
	position: absolute;
	z-index: 7;
	top: 0;
	bottom: 0;
	background: transparent;
	color: white;
}
.interact {
	z-index: -5;
}
.no__interact {
	z-index: 3;
}
