.comingSoon {
	width: 100vw;
	height: 100vh;
	display: flex;
	align-items: center;
	flex-direction: column;
	gap: 2rem;
	background-image: url("https://esquilo.io/wallpaper/wallpaper/20210703/nature-wallpaper-background-nature-hd-wallpapers-1080p-wallpaper-preview.webp");
	background-size: cover;
	background-repeat: no-repeat;
	color: white;
}
.comingSoon__label {
	font-size: 70px;
	color: white;
	width: 100%;
	text-align: center;
	background: linear-gradient(to right, #87ceff20, #ffff1944, #87ceff20),
		no-repeat center center;
	display: flex;
	flex-direction: column;
}
.rainbowWrap {
	font-size: 8rem;
	background: linear-gradient(
		45deg,
		#ff2400,
		#e81d1d,
		#e8b71d,
		#e3e81d,
		#1de840,
		#1ddde8,
		#2b1de8,
		#dd00f3,
		#dd00f3
	);
	background-size: 1800% 1800%;
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
	-webkit-animation: rainbow 30s ease infinite;
	-o-animation: rainbow 30s ease infinite;
	animation: rainbow 30s ease infinite;
	background-color: rgba(250, 250, 250, 0.349);
	backdrop-filter: blur(2px);
	width: 100%;
}
.comingSoon__logo {
	width: 10rem;
	/* background-color: rgba(255, 255, 255, 0.349); */
	/* backdrop-filter: blur(2px); */
	margin-top: 2rem;
}
@-webkit-keyframes rainbow {
	0% {
		background-position: 0% 82%;
	}
	50% {
		background-position: 100% 19%;
	}
	100% {
		background-position: 0% 82%;
	}
}
@-moz-keyframes rainbow {
	0% {
		background-position: 0% 82%;
	}
	50% {
		background-position: 100% 19%;
	}
	100% {
		background-position: 0% 82%;
	}
}
@-o-keyframes rainbow {
	0% {
		background-position: 0% 82%;
	}
	50% {
		background-position: 100% 19%;
	}
	100% {
		background-position: 0% 82%;
	}
}
@keyframes rainbow {
	0% {
		background-position: 0% 82%;
	}
	50% {
		background-position: 100% 19%;
	}
	100% {
		background-position: 0% 82%;
	}
}
