.store {
	display: flex;
	flex-direction: column;
	align-items: center;
	width: 100%;
	height: max-content;
}
.store__main {
	display: flex;
	flex-direction: column;
	align-items: center;
	/* min-height: 100vh; */
	gap: 1rem;
	padding-top: 4rem;
	width: 90%;
}
.store__feature {
	height: 25vh;
}
.store__filter {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	gap: 1rem;
	background-color: white;
	border-radius: 1rem;
	padding: 1rem 1rem;
	width: 100%;
}
.store__items {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	width: 100%;
	gap: 1rem;
	background-color: var(--neutral-2);
}

.store__misc,
.items__vehicles {
	display: flex;
	width: 100%;
	height: max-content;
	justify-content: center;
	align-items: center;
	gap: 1rem;
	background-color: white;
	border-radius: 1rem;
	padding: 1rem 1rem;
}
.store__misc {
	flex-direction: column;
}
.items__vehicles {
	flex-wrap: wrap;
}
.store__misc {
}
.filter__search {
	display: flex;
	flex-direction: row;
	align-items: center;
	position: relative;
	gap: 1rem;
	width: 80%;
}
.filter__search > label {
}
.filter__search > input[type="search"] {
	padding: 0.4rem 0.4rem;
	font-size: 1.2rem;
	/* min-width: 64ch; */
	width: 100%;
	border-radius: 0.5rem;
	outline: none;
	border: 1px solid rgba(168, 166, 166, 0.719);
}
#search-icon-btn {
	position: absolute;
	right: 2rem;
	z-index: 4;
}
#search-icon {
	position: absolute;
}
.filter__options > div {
	display: flex;
	flex-direction: row;
	align-items: center;
	width: 100%;
	gap: 0.5rem;
	border-bottom: 1px solid rgb(151, 148, 148);
	border-left: 1px solid rgb(151, 148, 148);
	padding: 0.1rem 0;
	padding-left: 0.2rem;
}
@media screen and (max-width: 768px) {
	.filter__options {
		display: flex;
		flex-direction: column;
		align-items: center;
		gap: 0.4rem;
		width: 100%;
	}
	.store__main {
		width: 90%;
	}
}
@media screen and (min-width: 768px) {
	.filter__options {
		display: grid;
		grid-template-columns: 1fr 1fr;
		column-gap: 1rem;
		row-gap: 0.4rem;
	}
	.store__main {
		width: 90%;
	}
}
