* {
	margin: 0;
	padding: 0;
	color: hsl(203, 6%, 26%);

	--p: hsl(219, 63%, 36%);
	--s: hsl(50, 100%, 47%);
	--a: hsl(203, 6%, 26%);

	--primary-color: #4169e1;
	/* --primary-color-hsl: 219, 63%, 36%; */
	--primary-color-hsl: 225, 73%, 57%;
	--primary-color-a1: hsla(var(--primary-color-hsl), 0.1);
	--primary-color-a2: hsla(var(--primary-color-hsl), 0.2);
	--primary-color-a3: hsla(var(--primary-color-hsl), 0.3);
	--primary-color-a4: hsla(var(--primary-color-hsl), 0.4);
	--primary-color-a5: hsla(var(--primary-color-hsl), 0.5);
	--primary-color-a6: hsla(var(--primary-color-hsl), 0.6);
	--primary-color-a7: hsla(var(--primary-color-hsl), 0.7);
	--primary-color-a8: hsla(var(--primary-color-hsl), 0.8);
	--primary-color-a9: hsla(var(--primary-color-hsl), 0.9);

	--secondary-color: hsl(203, 6%, 26%);
	--secondary-color-hsl: 203, 6%, 26%;
	--secondary-color-a1: hsla(var(--secondary-color-hsl), 0.1);
	--secondary-color-a2: hsla(var(--secondary-color-hsl), 0.2);
	--secondary-color-a3: hsla(var(--secondary-color-hsl), 0.3);
	--secondary-color-a4: hsla(var(--secondary-color-hsl), 0.4);
	--secondary-color-a5: hsla(var(--secondary-color-hsl), 0.5);
	--secondary-color-a6: hsla(var(--secondary-color-hsl), 0.6);
	--secondary-color-a7: hsla(var(--secondary-color-hsl), 0.7);
	--secondary-color-a8: hsla(var(--secondary-color-hsl), 0.8);
	--secondary-color-a9: hsla(var(--secondary-color-hsl), 0.9);

	--accent-1: hsl(50, 100%, 47%);
	--accent-1-hsl: 50, 100%, 47%;
	--accent-1-a1: hsla(var(--accent-1-hsl), 0.1);
	--accent-1-a2: hsla(var(--accent-1-hsl), 0.2);
	--accent-1-a3: hsla(var(--accent-1-hsl), 0.3);
	--accent-1-a4: hsla(var(--accent-1-hsl), 0.4);
	--accent-1-a5: hsla(var(--accent-1-hsl), 0.5);
	--accent-1-a6: hsla(var(--accent-1-hsl), 0.6);
	--accent-1-a7: hsla(var(--accent-1-hsl), 0.7);
	--accent-1-a8: hsla(var(--accent-1-hsl), 0.8);
	--accent-1-a9: hsla(var(--accent-1-hsl), 0.9);

	--accent-1-l1: hsl(50, 100%, 10%);
	--accent-1-l2: hsl(50, 100%, 20%);
	--accent-1-l3: hsl(50, 100%, 30%);
	--accent-1-l4: hsl(50, 100%, 40%);
	--accent-1-l5: hsl(50, 100%, 50%);
	--accent-1-l6: hsl(50, 100%, 60%);
	--accent-1-l7: hsl(50, 100%, 70%);
	--accent-1-l8: hsl(50, 100%, 80%);
	--accent-1-l9: hsl(50, 100%, 90%);

	--accent-2-hsl: 18, 53%, 24%;
	--accent-2: hsl(var(--accent-2-hsl));
	--accent-2-100: hsla(var(--accent-2-hsl), 0.1);
	--accent-2-200: hsla(var(--accent-2-hsl), 0.2);
	--accent-2-300: hsla(var(--accent-2-hsl), 0.3);
	--accent-2-400: hsla(var(--accent-2-hsl), 0.4);
	--accent-2-500: hsla(var(--accent-2-hsl), 0.5);
	--accent-2-600: hsla(var(--accent-2-hsl), 0.6);
	--accent-2-700: hsla(var(--accent-2-hsl), 0.7);
	--accent-2-800: hsla(var(--accent-2-hsl), 0.8);
	--accent-2-900: hsla(var(--accent-2-hsl), 0.9);

	--neutral-1: hsl(0, 0%, 0%);
	--neutral-2: hsl(0, 0%, 100%);
}

html {
	font-size: 16px;
}
@media screen and (min-width: 320px) {
	html {
		font-size: calc(12px + 6 * ((100vw - 320px) / 680));
	}
}
@media screen and (min-width: 1080px) {
	html {
		font-size: calc(12px + 6 * ((100vw - 1080px) / 680));
	}
}
@media screen and (min-width: 2000px) {
	html {
		font-size: calc(18px + 6 * ((100vw - 2000px) / 680));
	}
}
:root {
	padding: 0;
	margin: 0;
}
body {
	max-width: 100%;
	padding: 0;
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
		"Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
		"Helvetica Neue", sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background-color: var(--neutral-2);
	min-height: 100vh;
}
.no-scroll {
	overflow: hidden;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
		monospace;
}
