.loadIndicator {
	display: flex;
	position: absolute;
	enable-background: new 0 0 273.2 261;
	/* border: 2px saddlebrown solid; */
	top: 100%;
	justify-content: center;
	align-items: center;
}
.loading {
	display: flex;
	align-items: center;
	justify-content: center;
	width: 25%;
	height: 25%;
}
.loadIndicator__circle > p {
	position: absolute;
	z-index: 10;
	color: black;
	font-size: 1.5rem;
}
.loadIndicator__circle {
	/* fill: transparent;
	stroke: #333333;
	stroke-width: 4;
	stroke-miterlimit: 10; */
	position: relative;
	/* width: 15rem;  */
	width: calc((40vh + 40vw) / 2.8);
	height: calc((40vh + 40vw) / 2.8);
	/* height: 15rem; */
	border-radius: 15rem;
	background-color: transparent;
	border: calc((2vh + 2vw) / 5) solid black;
	display: flex;
	align-items: center;
	justify-content: center;
	overflow: hidden;
}
.loadIndicator__circle > p {
	font-size: calc((2vh + 2vw) / 0.8);
	text-align: center;
}
.loadIndicator__wave1 {
	opacity: 0.25;
	z-index: 8;
	fill: #0d99da;
	animation: wave1-swish infinite linear 7s;
}
.loadIndicator__wave2 {
	opacity: 0.27;
	z-index: 8;
	fill: #0d99da;
	animation: wave2-swish infinite linear 7s;
}
@keyframes wave1-swish {
	0% {
		transform: translateX(35%);
	}
	100% {
		transform: translateX(-35%);
	}
}
@keyframes wave2-swish {
	0% {
		transform: translateX(-35%);
	}
	100% {
		transform: translateX(35%);
	}
}
#eIRC7Pt35cS18_tr {
	animation: eIRC7Pt35cS18_tr__tr 2000ms linear infinite normal forwards;
}
@keyframes eIRC7Pt35cS18_tr__tr {
	0% {
		transform: translate(236.722535px, 165.047562px) rotate(0deg);
	}
	50% {
		transform: translate(236.722535px, 165.047562px) rotate(359.902312deg);
	}
	100% {
		transform: translate(236.722535px, 165.047562px) rotate(359.902312deg);
	}
}
#eIRC7Pt35cS18_ts {
	animation: eIRC7Pt35cS18_ts__ts 2000ms linear infinite normal forwards;
}
@keyframes eIRC7Pt35cS18_ts__ts {
	0% {
		transform: scale(0.2, 0.2);
	}
	50% {
		transform: scale(1, 1);
	}
	100% {
		transform: scale(1, 1);
	}
}
