		transform: translate(216.031382px, 297.5px) rotate(-130deg);
	}
	25% {
		transform: translate(216.031382px, 297.5px) rotate(0deg);
	}
	30% {
		transform: translate(216.031382px, 297.5px) rotate(20deg);
	}
