.footer {
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
	height: max-content;
	position: relative;
}
.footer__top {
	width: 100%;
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: center;
	background-color: var(--primary-color);
}
.footer__joinEmailList {
	width: max-content;
	padding: 1rem;
	gap: 0.4rem;
	align-items: center;
	justify-content: center;
	display: flex;
}
.footer__joinEmailList > input {
	height: 3ch;
	border-radius: 0.4rem;
	padding: 0.2rem 0.4rem;
	outline: none;
	width: 30ch;
	border: var() solid 1px;
}
.footer__joinEmailList > button {
	border-radius: 0.4rem;
	padding: 0.4rem 0.8rem;
	border: var(--secondary-color) solid 1px;
}
.footer__info {
	position: relative;
	width: 100%;
}
.footer__address,
.footer__name,
.footer__contact,
.footer__hours , .footer__social{
	text-align: left;
}
.footer__logo {
	position: relative;
	width: 12rem;
}
.footer__bottom {
	background-color: var(--primary-color-a8);
	display: flex;
	width: 100%;
	flex-direction: column;
	align-items: center;
}
.footer__bottom * {
	color: white;
}
.footer__bottom > div {
	display: grid;
	width: 90%;
	gap: 1rem;
	grid-template-columns: repeat(3, 30%);

	align-items: center;
}
.footer__bottom > p {
	width: 100%;
	text-align: center;
	background-color: #252525;
	font-weight: 300;
	color: white;
	padding: 0.4rem;
	font-size: 0.7rem;
}
